import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Detail from "./routers/Detail";
import Home from "./routers/Home";

function App() {
  return (
  <Router>
    {/*   route  Switch */}
    <Switch>
    <Route path="/abot-us">
          <h1>Hello</h1>
        </Route>
        {/* react Router  id  ~ */}
      <Route path ="/movie/:id">
        <Detail />
      </Route>
      <Route path="/">
        <Home />
      </Route>
    </Switch>
  </Router>
  );
}

export default App;
